import React, { Component } from 'react'
import MakeWithdraw from '../../../ethereum/MakeWithdraw'
import '../Home.css'

class Withdraw extends Component {
  constructor(props) {
    super(props)
    this.state = {
      note: '',
      recipient: '',
    }
    this.handleChange = this.handleChange.bind(this)
    this.handleDonate = this.handleDonate.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
    this.handleReq = this.handleReq.bind(this)
  }
  
  handleChange(e) {
    this.setState({ [e.target.name]: e.target.value })
  }
  
  handleDonate() {
    const { REACT_APP_ETH_TORNADO_ADDRESS } = process.env
    this.setState({ recipient: REACT_APP_ETH_TORNADO_ADDRESS })
  }
  
  handleSubmit(e) {
    e.preventDefault()
    const { note, recipient } = this.state
    if (note.length > 0 && recipient.length > 0) {
      this.handleReq({ note: note, recipient: recipient })
    }
  }
  
  async handleReq({...args}) {
    await MakeWithdraw(args.note, args.recipient)
  }
  
  render() {
    const { recipient, note } = this.state
    return (
      <form onSubmit={this.handleSubmit} style={{ display: 'flex', flexDirection: 'column', width: '25vw' }}>
        <div style={{ padding: '0 1vw' }}>Note</div>
        <input
          type={'text'}
          name={'note'}
          value={note}
          onChange={this.handleChange}
          className={'withDrawInput'}/>
        <div style={{
          display: 'flex',
          justifyContent: 'space-between',
          padding: '0 1vw',
          margin: '0.5vh 0',
        }}>
          <div>Recipient Address</div>
          <div style={{ color: '#1F33E8', cursor: 'pointer' }} onClick={this.handleDonate}>
            Donate
          </div>
        </div>
        <input
          type={'text'}
          name={'recipient'}
          value={recipient}
          onChange={this.handleChange}
          className={'withDrawInput'}/>
        <input
          type={'submit'}
          value={'Withdraw'}
          className={'homeConnect'}
          style={{
            // width: '80px', // for mobile?
            width: '10vw',
            margin: '5vh 0',
            alignSelf: 'center',
            color: '#D7DADC',
            border: '1px solid #1F33E8',
            padding: '1vw 1vh',
            textAlign: 'center',
            borderRadius: '2px',
            backgroundColor: 'transparent'
          }}/>
      </form>
    )
  }
}

export default Withdraw
