import circomlib from 'circomlib'
import ETHTornado from './ETHTornado'
import crypto from 'crypto'
import web3 from '../web3'
import bigInt from 'snarkjs/src/bigint'

let netId

/** Generate random number of specified byte length */
const rbigint = (nbytes) => (
  bigInt.leBuff2int(crypto.randomBytes(nbytes))
)

/** Compute pedersen hash */
const pedersenHash = (data) => (
  circomlib.babyJub.unpackPoint(circomlib.pedersenHash.hash(data))[0]
)

/** BigNumber to hex string of specified length */
const toHex = (numb, length = 32) => (
  '0x' + (
    numb instanceof Buffer ? numb.toString('hex') : bigInt(numb).toString(16)
  ).padStart(length * 2, '0')
)


/**
 * Create deposit object from secret and nullifier
 */
function makeDeposit(nullifier, secret) {
  let deposit = { nullifier, secret }
  deposit.preimage = Buffer.concat([deposit.nullifier.leInt2Buff(31), deposit.secret.leInt2Buff(31)])
  deposit.commitment = pedersenHash(deposit.preimage)
  deposit.nullifierHash = pedersenHash(deposit.nullifier.leInt2Buff(31))
  return deposit
}

/**
 * Make an ETH deposit
 * @param AMT the deposit amount
 */
async function deposit(AMT) {
  const deposit = await makeDeposit(rbigint(31), rbigint(31))
  console.log('Sending deposit transaction...')
  const commitmentAsHex = await toHex(deposit.commitment)
  // console.log('asHex', asHex)
  const accounts = await web3.eth.getAccounts()
  console.log('accounts', accounts)
  const asWei = web3.utils.toWei(AMT, 'ether')
  const tx = await ETHTornado.methods.deposit(commitmentAsHex).send({
    value: asWei,
    from: accounts[0],
    gas: 2e6
  })
  console.log(`https://rinkeby.etherscan.io/tx/${tx.transactionHash}`)
  return `cardnado-eth-${AMT}-${netId}-${toHex(deposit.preimage, 62)}`
}

async function MakeDeposit(denom) {
  netId = await web3.eth.net.getId()
  const note = await deposit(denom)
  console.log('note:', note)
  return note
}

export default MakeDeposit

